<template>
  <div>
    <div class="flex flex-between topline">
      <h2 class="title"></h2>
      <el-button type="success" @click="showAddCompany = true" icon="el-icon-check"></el-button>
    </div>

    <div class="company_list">
      <el-table :data="company_list" style="width: 400px">
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column align="right">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="deleteMKK(scope.row._id)">
              <i class="el-icon-delete"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="add_company modal" v-if="showAddCompany">
      <el-form ref="form" class="form" label-width="120px">
        <h3 class="modal_title">  </h3>
        <el-form-item label=": ">
          <el-input v-model="mkk_name" placeholder="  "></el-input>
        </el-form-item>
        <el-form-item class="step_btn_list">
          <el-button type="primary" @click.prevent="createMKK"></el-button>
          <el-button type="cancel" @click.prevent="showAddCompany = false"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "Companies",
  data() {
    return {
      company_list: [],
      mkk_name: "",
      showAddCompany: false
    };
  },
  props: {},
  methods: {
    async getMKK() {
      try {
        let result = await this.$axios.get("/mkk/getMkk");
        if (result.status == 200) {
          this.company_list = result.data;
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
    async createMKK() {
      try {
        let data = { name: this.mkk_name };
        let result = await this.$axios.post("/mkk/createMkk", data);
        if (result.status == 200 || result.status == 201) {
          this.company_list.push(result.data);
          this.mkk_name = "";
          this.showAddCompany = false;
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
    async deleteMKK(id) {
      let answer = confirm(" ,   ?");
      if (answer) {
        try {
          let result = await this.$axios.delete("/mkk/deleteMkk", {
            data: { mkkId: id }
          });
          if (result.status == 200 || result.status == 201) {
            this.company_list = this.company_list.filter(i => {
              return i._id != id;
            });
            //  await this.getMKK();
          }
        } catch (e) {
          console.log("[ERR]", e);
        }
      }
    }
  },
  async mounted() {
    await this.getMKK();
  }
};
</script>

<style scoped>
.company_list ul {
  width: 100%;
  max-width: 500px;
}
.company_list ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 10px;
  border-bottom: 1px solid #d0d0d0;
}
.el-button.is-circle {
  padding: 5px;
}
.form {
  max-width: 500px;
  width: 100%;
  background: #fff;
  padding: 20px 30px;
  border-radius: 15px;
}
.el-input__inner {
  height: 32px;
  line-height: 32px;
}
.modal {
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 90;
  width: 100%;
  height: 100%;
}
.modal_title {
  text-align: center;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #efefef;
}
</style>