<template>
  <div>
    <div class="flex flex-between topline">
      <h2 class="title"></h2>
      <router-link class="btn main-btn" to="/add-client">
        <span> </span>
      </router-link>
    </div>
    <el-table :data="clientlist" style="width: 100%">
      <el-table-column label="" prop="inn"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span v-if="scope.row.name">{{ scope.row.name }}</span>
          <span v-else-if="scope.row.firstName">{{ scope.row.lastName + ' ' + scope.row.firstName }}</span>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column align="right">
        <template slot-scope="scope">
          <el-button
            @click="getClientTransaction(scope.row.clntSrcCftId)"
            size="mini"
            type="primary"
          >
            <i class="el-icon-info"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div v-if="showClientHistory" class="fixed-modal modal">
      <div class="modal_content">
        <el-table :data="clientHistory" class="history_list">
          <el-table-column label="" prop="cardPan"></el-table-column>
          <el-table-column label="" prop="amount"></el-table-column>
          <el-table-column label="" prop="createdAt"></el-table-column>
        </el-table>
        <div class="btn_list">
          <el-button
            class="close-btn"
            type="cancel"
            @click.prevent="showClientHistory = false"
          ></el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "Clients",
  data() {
    return {
      clientlist: [],
      clientHistory: [],
      showClientHistory: false
    };
  },
  props: {},
  methods: {
    async getClients() {
      try {
        let result = await this.$axios.get("/ibs/getClients");
        if (result.status == 200) {
          this.clientlist = result.data;
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
    async getClientTransaction(client_id) {
      try {
        let url = "/ibs/getTransactions?clntSrcCftId=" + client_id;

        let result = await this.$axios.get(url);

        if (result.status == 200) {
          this.showClientHistory = true;
          this.clientHistory = result.data;
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
    cardNumberInTable(num) {
      num = num + "1";
      return num;
    }
  },
  async mounted() {
    await this.getClients();
  }
};
</script>

<style scoped>
.main-btn {
  background: #67c23a;
  color: #fff;
  text-decoration: none;
}
.history_list {
  width: 100%;
  min-width: 600px;
}
.btn_list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 15px 0;
}
</style>