<template>
  <div>
    
    <div class="flex flex-between topline">
    <h2 class="title"></h2>
    
    </div>
    <div>
      <el-table :data="transactionlist" class="history_list">
          <el-table-column  label="" prop="cardPan"></el-table-column>
          <el-table-column label="" prop="amount"></el-table-column>
          <el-table-column label="" prop="createdAt"></el-table-column>

          
        </el-table>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "Transactions",
  data() {
    return {
        transactionlist: []
    };
  },
  props: {},
  methods: {
async getTransactions() {
      try {
        let result = await this.$axios.get("/mkk/getTransactions");
        if (result.status == 200) {
          this.transactionlist = result.data;
          console.log(result.data);
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
  },
  async mounted() {
    await this.getTransactions();
  }
};
</script>

<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>