import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/views/Index'
import Clients from '@/views/Clients'
import Companies from '@/views/Companies'
import Requests from '@/views/Requests'
import Transactions from '@/views/Transactions'
import Users from '@/views/Users'
import Settings from '@/views/Settings'
import Login from '@/views/Login'
import ErrorPage from '@/views/ErrorPage'
import AddClient from '@/views/AddClient'


Vue.use(Router)

const router = new Router({
  mode: 'hash',
  routes: configRoutes()
})

function configRoutes() {
  return [
    {
      path: '/',
      name: '',
      meta: {
        auth: true
      },
      component: Index,
      children: [
        {
          path: '/clients',
          name: 'Clients',
          meta: { auth: true },
          component: Clients
        },
        {
          path: '/transactions',
          name: 'Transactions',
          meta: { auth: true },
          component: Transactions
        },
        {
          path: '/companies',
          name: 'Companies',
          meta: { auth: true },
          component: Companies
        },
        {
          path: '/settings',
          name: 'Settings',
          meta: { auth: true },
          component: Settings
        },
        {
          path: '/users',
          name: 'Users',
          meta: { auth: true },
          component: Users
        },
        {
          path: '/requests',
          name: 'Requests',
          meta: { auth: true },
          component: Requests
        },
        {
          path: '/add-client',
          name: 'AddClient',
          meta: { auth: true },
          component: AddClient
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      meta: { auth: false },
      component: Login
    },
    {
      path: '*',
      name: 'ErrorPage',
      meta: { auth: false },
      component: ErrorPage
    },
  ]
}

router.beforeEach((to, from, next) => {
  
  let token = Vue.ls.get('token', null);
  let requiredAuth = to.matched.some(r => r.meta.auth)

  if (requiredAuth && (!token)){
    next('login');
    this.$localStorage.clear(); 
  } 
  else next();
});

export default router;