<template>
  <div>
    <div class="flex flex-between topline">
      <h2 class="title"></h2>
      <el-button type="success" @click="showAddUser = true,getMKK()" icon="el-icon-check"></el-button>
    </div>

    <div class="userlist">
      <el-table :data="userlist" style="width: 100%">
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="email" prop="email"></el-table-column>
        <el-table-column label="" prop="company_name"></el-table-column>
        <el-table-column label="" prop="role"></el-table-column>
        <el-table-column align="right">
          <template slot-scope="scope">
            <el-button @click="getUser(scope.row.id)" size="mini" type="primary">
              <i class="el-icon-edit"></i>
            </el-button>
            <el-button size="mini" type="danger" @click="deleteUser(scope.row.id)">
              <i class="el-icon-delete"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div v-if="showAddUser" class="modal">
      <el-form ref="form" class="form" label-width="120px">
        <h3 class="modal_title">  </h3>
        <el-form-item label=" ">
          <el-input v-model="user_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="email" v-model="user_email"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="user_password"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="user_mkk_value" placeholder="">
            <el-option
              v-for="item in company_list"
              :key="item._id"
              :label="item.name"
              :value="item._id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="step_btn_list">
          <el-button type="primary" @click.prevent="createUser"></el-button>
          <el-button type="cancel" @click.prevent="showAddUser = false"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "Users",
  data() {
    return {
      showAddUser: false,
      company_list: [],
      userlist: [],
      user_name: "",
      user_email: "",
      user_password: "",
      user_mkk_value: ""
    };
  },
  props: {},
  methods: {
    async getMKK() {
      try {
        let result = await this.$axios.get("/mkk/getMkk");
        if (result.status == 200) {
          this.company_list = result.data;
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
    async getUsers() {
      try {
        await this.getMKK();
        let result = await this.$axios.get("/users");

        if (result.status == 200) {
          this.userlist = result.data.results;

          this.userlist = this.userlist.map(user => {
            user.company_name = "";
            if (user.mkk) {
              let company = this.company_list.filter(i => {
                return i._id == user.mkk;
              });
              if (company) user.company_name = company[0].name;
            }
            return user;
          });
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
    async createUser() {
      try {
        let data = {
          name: this.user_name,
          email: this.user_email,
          password: this.user_password,
          mkk: this.user_mkk_value,
          role: "user"
        };
        let result = await this.$axios.post("/users", data);
        console.log(result);
        if (result.status == 200 || result.status == 201) {
          this.getUsers();
          this.showAddUser = false;
        }
      } catch (e) {
        if (e.response) {
          alert(e.response.data.message);
        }
      }
    },
    async deleteUser(id) {
      let answer = confirm(" ,   ?");
      if (answer) {
        try {
          let result = await this.$axios.delete("/users/" + id);
          if (result.status == 204) {
            this.userlist = this.userlist.filter(i => {
              return i.id != id;
            });
          }
        } catch (e) {
          console.log("[ERR]", e);
        }
      }
    },
    async updateUser(id, name, email, password) {
      try {
        let data = {};
        let result = await this.$axios.patch("/users/" + id, data);
        if (result.status == 204) {
          this.userlist = this.userlist.filter(i => {
            return i.id != id;
          });
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
    async getUser(id) {
      try {
        let result = await this.$axios.get("/users/" + id);
        if (result.status == 204 || result.status == 200) {
          console.log("User: ", result);
          this.showAddUser = true;
          this.user_email = result.data.email;
          this.user_name = result.data.name;
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    }
  },
  async mounted() {
    await this.getUsers();
  }
};
</script>

<style scoped>
.el-select {
  width: 100%;
}
.form {
  max-width: 500px;
  width: 100%;
  background: #fff;
  padding: 20px 30px;
  border-radius: 15px;
}
</style>