<template>
  <div>
    <h2> </h2>
    <div class="add_client_form">
      <el-form class="form" v-if="showAddCard == ''" label-width="140px">
        <el-form-item label="">
          <el-input
            type="text"
            v-mask="'##############'"
            v-model="client_inn"
            maxlength="14"
            minlength="14"
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click.prevent="checkClient"
            :disabled="this.client_inn.length < 14"
          ></el-button>
        </el-form-item>
      </el-form>

      <div class="new_client_info" v-if="showAddCard == 'not_found'">
        <div class="step_list">
          <div class="step_item" v-bind:class="{ 'active': this.current_step == '1'}"> </div>
          <div class="step_item" v-bind:class="{ 'active': this.current_step == '2'}"></div>
          <div class="step_item" v-bind:class="{ 'active': this.current_step == '3'}"></div>
          <div class="step_item" v-bind:class="{ 'active': this.current_step == '4'}"></div>
          <div class="step_item" v-bind:class="{ 'active': this.current_step == '5'}"></div>
        </div>
        <div class="step_content step_1" v-bind:class="{ 'show': this.current_step == '1'}">
          <el-form class="form" label-width="200px">
            <!-- <el-form class="form"  label-width="200px"> -->
            <el-form-item label=":">
              <el-input type="text" readonly v-model="client_inn" placeholder="  "></el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-input v-model="client_lastname" placeholder=" "></el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-input v-model="client_firstname" placeholder=" "></el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-input v-model="client_midname" placeholder=" "></el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-radio-group v-model="client_sex">
                <el-radio :label="2047936"></el-radio>
                <el-radio :label="2047935" ></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label=" :">
              <el-select
                class="w50 mr15"
                v-model="selected_region"
                @change="getCityFromRegion"
                filterable
                placeholder=" "
              >
                <el-option
                  v-for="item in client_birth_place_region_option"
                  :key="item.REGN_ID"
                  :label="item.REGN_NAME"
                  :value="item.REGN_ID"
                ></el-option>
              </el-select>
              <el-select
                class="w50"
                v-model="client_birth_place"
                filterable
                placeholder=" "
              >
                <el-option
                  v-for="item in client_birth_filtered_place_option"
                  :key="item.CITY_SRC_CFT_ID"
                  :label="item.CITY_NAME"
                  :value="item.CITY_SRC_CFT_ID"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label=" :">
              <el-date-picker
                v-model="client_birthdate"
                type="date"
                placeholder=" "
                format="dd/MM/yyyy"
                value-format="yyyyMMdd"
                :picker-options="pickBirthDateOptions"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label=" :">
              <el-select v-model="client_marriage_status" placeholder="">
                <el-option
                  v-for="item in client_marriage_options"
                  :key="item.FMLT_SRC_CFT_ID"
                  :label="item.FMLT_NAME"
                  :value="item.FMLT_SRC_CFT_ID"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=":">
              <el-radio-group v-model="client_ipdl">
                <el-radio :label="1"></el-radio>
                <el-radio :label="2"></el-radio>
                <el-radio :label="3"> </el-radio>
                <el-radio :label="4"> </el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item class="step_btn_list">
              <el-button class="step_btn" type="success" @click.prevent="createClient"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="step_content step_2" v-bind:class="{ 'show': this.current_step == '2'}">
          <el-form class="form" label-width="250px">
            <el-form-item label=" :">
              <el-radio-group v-model="client_passport_type">
                <el-radio :label="22636059">ID </el-radio>
                <el-radio :label="6976603642">ID () . 2017</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label=" :">
              <el-select v-model="client_passport_seria" placeholder=" ">
                <el-option
                  v-for="item in client_passport_seria_options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=" :">
              <el-input
                v-model="client_passport_num"
                type="text"
                v-mask="'#######'"
                placeholder=" ( )"
              ></el-input>
            </el-form-item>
            <el-form-item label=",  :">
              <el-input
                v-model="client_passport_who"
                type="text"
                placeholder="  ,  "
              ></el-input>
            </el-form-item>
            <el-form-item label=" :">
              <el-date-picker
                v-model="client_passport_date_start"
                type="date"
                placeholder=" "
                format="dd/MM/yyyy"
                value-format="yyyyMMdd"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="  :">
              <el-date-picker
                v-model="client_passport_date_finish"
                type="date"
                placeholder=" "
                format="dd/MM/yyyy"
                value-format="yyyyMMdd"
              ></el-date-picker>
            </el-form-item>
            <el-form-item class="step_btn_list">
              <el-button class="step_btn" type="success" @click.prevent="createDoc"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="step_content step_3" v-bind:class="{ 'show': this.current_step == '3'}">
          <el-form class="form" label-width="250px">
            <h3> :</h3>
            <el-form-item label=" :">
              <el-input
                type="text"
                v-model="client_address_corp_zip"
                placeholder="720000"
                v-mask="'######'"
              ></el-input>
            </el-form-item>
            <el-form-item label=" :">
              <el-select
                class="w100"
                v-model="client_address_corp_city"
                filterable
                placeholder="  "
              >
                <el-option
                  v-for="item in client_birth_place_option"
                  :key="item.CITY_SRC_CFT_ID"
                  :label="item.CITY_NAME"
                  :value="item.CITY_SRC_CFT_ID"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=":">
              <el-input
                type="text"
                v-model="client_address_corp_street"
                placeholder=" "
              ></el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-input
                type="text"
                v-model="client_address_corp_house"
                placeholder="  "
              ></el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-input
                type="text"
                v-model="client_address_corp_flat"
                placeholder="  "
              ></el-input>
            </el-form-item>
            <el-form-item label>
              <el-checkbox
                v-model="address_identical"
              >     ?</el-checkbox>
            </el-form-item>
            <div class="actual_address" v-if="!address_identical">
              <h3> :</h3>
              <el-form-item label=" :">
                <el-input
                  type="text"
                  v-model="client_address_fact_zip"
                  placeholder="720000"
                  v-mask="'######'"
                ></el-input>
              </el-form-item>
              <el-form-item label=" :">
                <el-select
                  class="w100"
                  v-model="client_address_fact_city"
                  filterable
                  placeholder="  "
                >
                  <el-option
                    v-for="item in client_birth_place_option"
                    :key="item.CITY_SRC_CFT_ID"
                    :label="item.CITY_NAME"
                    :value="item.CITY_SRC_CFT_ID"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label=":">
                <el-input
                  type="text"
                  v-model="client_address_fact_street"
                  placeholder=" "
                ></el-input>
              </el-form-item>
              <el-form-item label=":">
                <el-input
                  type="text"
                  v-model="client_address_fact_house"
                  placeholder="  "
                ></el-input>
              </el-form-item>
              <el-form-item label=":">
                <el-input
                  type="text"
                  v-model="client_address_fact_flat"
                  placeholder="  "
                ></el-input>
              </el-form-item>
            </div>
            <el-form-item class="step_btn_list">
              <el-button class="step_btn" type="success" @click.prevent="createAddressCorp()"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="step_content step_4" v-bind:class="{ 'show': this.current_step == '4'}">
          <el-form class="form" label-width="200px">
            <el-form-item label=" :">
              <el-input
                type="text"
                v-model="client_phone_number"
                v-mask="'+996 (###) ## ## ##'"
                placeholder="+996 (XXX) YY YY YY"
              ></el-input>
            </el-form-item>
            <el-form-item class="step_btn_list">
              <el-button class="step_btn" type="success" @click.prevent="swapStep(5)"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="step_content step_5" v-bind:class="{ 'show': this.current_step == '5'}">
          <el-form class="form" label-width="140px">
            <h3> </h3>
            <el-form-item label="">
              <el-input v-model="client_fullname" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input type="email" v-model="client_inn" disabled></el-input>
            </el-form-item>
            <el-form-item label=" ">
              <el-input v-model="card_pan" v-mask="'#### #### #### ####'"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="amount_sum" v-mask="'#########'"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click.prevent="mapCardToClient"
                :disabled="this.card_pan.length < 19 || this.amount_sum.length < 1"
              > </el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>

      <el-form v-if="showAddCard == 'found'" class="form" label-width="140px">
        <h3> </h3>
        <el-form-item label="">
          <el-input type="text" v-model="client_fullname" readonly></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="email" v-model="client_inn" readonly></el-input>
        </el-form-item>
        <el-form-item label=" ">
          <el-input v-model="card_pan" v-mask="'#### #### #### ####'"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="text" v-model="amount_sum" v-mask="'#########'"></el-input>
        </el-form-item>
        <el-input type="hidden" readonly v-model="r_clnt_src_cft_id"></el-input>
        <el-form-item>
          <el-button
            type="primary"
            @click.prevent="mapCardToClient"
            :disabled="this.card_pan.length < 19 || this.amount_sum.length < 1"
          > </el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="loader" v-if="this.processing">
      <i class="el-icon-loading"></i>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "AddClient",
  data() {
    return {
      pickBirthDateOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() - 567600000000;
        }
      },
      processing: false,
      current_step: "1",
      client_inn: "",
      client_lastname: "",
      client_fullname: "",
      client_firstname: "",
      client_midname: "",
      client_sex: "",
      client_resident: 1,
      client_birth_place: "",
      selected_region: "",
      client_birth_place_region: "",
      client_birth_place_option: [],
      client_birth_filtered_place_option: [],
      client_birth_place_region_option: [],
      client_birth_place_eng: "Test",
      client_birthdate: "",
      client_passport_seria: "",
      client_passport_seria_options: [
        { value: "AN", label: "AN" },
        { value: "ID", label: "ID" }
      ],
      client_passport_num: "",
      client_passport_type: "",
      client_passport_who: "",
      client_passport_date_start: "",
      client_passport_date_finish: "",
      client_birth_year: "",
      client_marriage_status: "",
      client_marriage_options: [],
      client_faillevel: "0",
      client_objective: " ",
      client_ipdl: "",
      client_category: "1037420476",
      card_pan: "",
      amount_sum: "",
      showAddCard: "",
      address_identical: true,
      client_address_corp_zip: "",
      client_address_corp_city: "",
      client_address_corp_house: "",
      client_address_corp_street: "",
      client_address_corp_flat: "",
      client_address_fact_zip: "",
      client_address_fact_city: "",
      client_address_fact_house: "",
      client_address_fact_street: "",
      client_address_fact_flat: "",

      client_phone_number: "",

      r_clnt_src_cft_id: "",

      sorted_places: [],
      date: ""
    };
  },
  props: {},
  methods: {
    getCityFromRegion() {
      let id = this.selected_region;
      var filtered_list = [];

      this.client_birth_filtered_place_option = this.client_birth_place_option.filter(
        x => {
          return x.REGN_ID == id;
        }
      );
    },
    swapStep(id) {
      this.current_step = id;
    },
    async checkClient() {
      try {
        this.processing = true;
        let url = "/ibs/getClient?inn=" + this.client_inn;
        let result = await this.$axios.get(url);
        if (result.status == 200) {
          this.processing = false;
          result = result.data;
          this.showAddCard = result.r_err_code;
          if (result.r_err_code == "found") {
            this.client_fullname = result.r_clnt_name;
            this.r_clnt_src_cft_id = result.r_clnt_src_cft_id;
            this.card_pan = "";
          } else if (result.r_err_code == "not_found") {
            //   
            if(this.client_inn[0] == '1'){
              this.client_sex = 2047936;
            }else if(this.client_inn[0] == '2'){
              this.client_sex = 2047935;
          
            }
            //    
            this.client_birthdate =
              this.client_inn.slice(5, 9) +
              this.client_inn.slice(3, 5) +
              this.client_inn.slice(1, 3);

            // 
            await this.getDictionary();
          }
        }
      } catch (e) {
        this.processing = false;
        console.log("[ERR]", e);
      }
    },
    async getDictionary() {
      try {
        let result = await this.$axios.get(
          "/ibs/getDictionary?dictName=family_types"
        );
        if (result.status == 200) {
          this.client_marriage_options = result.data;
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
      try {
        let result = await this.$axios.get(
          "/ibs/getDictionary?dictName=cities"
        );
        if (result.status == 200) {
          this.client_birth_place_option = result.data;
          
          var full_list = [];
          this.client_birth_place_option.filter(function(item) {
            var i = full_list.findIndex(x => x.REGN_ID == item.REGN_ID);
            if (i <= -1) {
              full_list.push(item);
            }
            return null;
          });

          this.client_birth_place_region_option = full_list;
        }
      } catch (e) {
        console.log("[ERR]", e);
      }
    },
    async createClient() {
      try {
        this.processing = true;
        let data = {
          inn: this.client_inn,
          resident: this.client_resident,
          birthDate: this.client_birthdate,
          mariageStatus: this.client_marriage_status,
          sex: this.client_sex,
          birthPlace: this.client_birth_place,
          birthPlaceEng: this.client_birth_place_eng,
          lastName: this.client_lastname,
          firstName: this.client_firstname,
          midName: this.client_midname,
          ipdl: this.client_ipdl,
          failLevel: this.client_faillevel,
          objective: this.client_objective,
          category: this.client_category
        };
        let url = "/ibs/createClient";

        let result = await this.$axios.post(url, data);

        if (result.status == 200 || result.status == 201) {
          this.processing = false;
          if (result.data.r_err_code == "OK") {
            this.swapStep(2);
            this.client_fullname =
              this.client_lastname +
              " " +
              this.client_firstname +
              " " +
              this.client_midname;
            this.r_clnt_src_cft_id = result.data.r_clnt_src_cft_id;
          }
        }
      } catch (e) {
        this.processing = false;
        console.log("[ERR]", e);
        if (e.response) {
          alert(e.response.data.message);
        }
      }
    },
    async createDoc() {
      try {
        this.processing = true;
        let data = {
          clntSrcCftId: String(this.r_clnt_src_cft_id),
          docNum: this.client_passport_num,
          docType: this.client_passport_type,
          docSer: this.client_passport_seria,
          docWho: this.client_passport_who,
          docDate: this.client_passport_date_start,
          docDateEnd: this.client_passport_date_finish,
          docBirthPlace: ""
        };
        let url = "/ibs/createDoc";

        let result = await this.$axios.post(url, data);

        if (result.status == 200 || result.status == 201) {
          this.processing = false;
          if (result.data.r_err_code == "OK") {
            this.swapStep(3);
          }
        }
      } catch (e) {
        this.processing = false;
        console.log("[ERR]", e);
        alert(e);
      }
    },
    async createAddressCorp() {
      try {
        this.processing = true;
        let data = {
          clntSrcCftId: String(this.r_clnt_src_cft_id),
          type: "2047919",
          zip: this.client_address_corp_zip,
          city: this.client_address_corp_city,
          house: this.client_address_corp_house,
          korpus: "-",
          flat: this.client_address_corp_flat,
          street: this.client_address_corp_street
        };
        let url = "/ibs/createAddress";

        let result = await this.$axios.post(url, data);

        if (result.status == 200 || result.status == 201) {
          if (result.data.r_err_code == "OK") {
            this.createAddressFact();
          }
        }
      } catch (e) {
        this.processing = false;
        alert(e);
      }
    },
    async createAddressFact() {
      try {
        let data = {};

        if (this.address_identical) {
          data = {
            clntSrcCftId: String(this.r_clnt_src_cft_id),
            type: "2047922",
            zip: this.client_address_corp_zip,
            city: this.client_address_corp_city,
            house: this.client_address_corp_house,
            korpus: "-",
            flat: this.client_address_corp_flat,
            street: this.client_address_corp_street
          };
        } else {
          data = {
            clntSrcCftId: String(this.r_clnt_src_cft_id),
            type: "2047922",
            zip: this.client_address_fact_zip,
            city: this.client_address_fact_city,
            house: this.client_address_fact_house,
            korpus: "-",
            flat: this.client_address_fact_flat,
            street: this.client_address_fact_street
          };
        }

        let url = "/ibs/createAddress";

        let result = await this.$axios.post(url, data);

        if (result.status == 200 || result.status == 201) {
          this.processing = false;
          if (result.data.r_err_code == "OK") {
            this.swapStep(4);
          }
        }
      } catch (e) {
        this.processing = false;
        console.log("[ERR]", e);
      }
    },
    async createContact() {
      try {
        this.processing = true;
        let data = {
          clntSrcCftId: String(this.r_clnt_src_cft_id),
          numb: this.client_phone_number
        };
        let url = "/ibs/createContact";

        let result = await this.$axios.post(url, data);

        if (result.status == 200 || result.status == 201) {
          this.processing = false;
          if (result.data.r_err_code == "OK") {
            this.swapStep(5);
          }
        }
      } catch (e) {
        this.processing = false;
        alert(e);
      }
    },
    async mapCardToClient() {
      try {
        this.processing = true;
        let url = "/ibs/mapCardToClient";
        let data = {
          clntSrcCftId: String(this.r_clnt_src_cft_id),
          cardPan: this.card_pan.replace(/\s+/g, ""),
          name: this.client_fullname,
          inn: this.client_inn,
          amount: parseFloat(this.amount_sum)
        };
        let result = await this.$axios.post(url, data);

        if (result.status == 200 || result.status == 201) {
          this.processing = false;
          if(result.response.r_err_code == 'OK'){
            alert("");
            this.$router.push("/clients");

          }else if(result.response.r_err_code == 'card_not_found'){
            alert("  .");

          }else if(result.response.r_err_code == 'incorrect_params'){
            alert(" ");
            
          }else if(result.response.r_err_code == 'incorrect_card_number'){
            alert("  ");
            
          }else if(result.response.r_err_code == 'card_mapped_to_client'){
            alert("   ");

          }else if(result.response.r_err_code == 'server_error'){
            alert(" ,  .");
            this.$router.push("/clients");

          }else{
            alert(" ,  .");
            this.$router.push("/clients");
          }
          
        }
        
      } catch (e) {
        this.processing = false;
        console.log("[ERR]", e);
      }
    }
  },
  async mounted() {}
};
</script>

<style scoped>
.add_client_form {
  width: 800px;
  padding: 40px 0;
}
.add_client_form h3 {
  margin: 0 0 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #d0d0d0;
}
.add_client_form form {
  margin-left: 0;
  width: 100%;
  max-width: 100%;
}
.mr15{
  margin-right:15px;
}
.w30 {
  width: 30%;
}
.w100 {
  width: 100%;
}
.flex-between {
  display: flex;
  justify-content: space-between;
}
.step_list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 30px;
}
.step_item {
  width: 25%;
