import axios from "axios";
import LocalStorage from '@/plugins/localStorage';
const localStorage = new LocalStorage();

if (!process.env.VUE_APP_API_MKK_URL) throw new Error('   ,  .env  : VUE_APP_API_MKK_URL');

let instance = axios.create({
    baseURL: process.env.VUE_APP_API_MKK_URL,
}); 

instance.interceptors.request.use((config) => {
    let token = localStorage.get('token', null)
    if (token) config.headers.Authorization = `Bearer ${token}`;
    return config
}, (error) => {
    return Promise.reject(error);
});

instance.interceptors.response.use((response) => {
    if (response && response.data && response.data.code && response.data.code == 401 && response.data.status == 'Unauthorized')
        window.location = '/#/login';
    return response;
}, (error) => {
    if (error.response && error.response.status && error.response.status == 401){
        alert(' ');
        window.location = '/#/login';
    }
    
    return Promise.reject(error);
});

export default {
    install(Vue) {
        Vue.prototype.$axios = instance;
    }
}