<template>
  <div class="hello">
    <h1></h1>
    <form class="login_form" @submit.prevent="onLogin">
      <div class="ab_group">
        <input class="ab_input" required v-model="username" type="text" />
        <span class="highlight"></span>
        <span class="bar"></span>
        <label></label>
      </div> 
      <div class="ab_group">
        <input class="ab_input" v-model="password" type="password" required />
        <span class="highlight"></span>
        <span class="bar"></span>
        <label></label>
      </div>
      <div class="ab_group">
        <el-button type="success" @click="onLogin"></el-button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    async onLogin() {
      try {
        let body = { email: this.username, password: this.password };

        let result = await this.$axios.post(
          "/auth/login",
          body
        ); 

        if (result.status == 200) {
         
          this.$setToken(result.data.tokens.access.token); 
          this.$setUser(result.data.user.name); 
          this.$localStorage.set('role', result.data.user.role );
          
          this.$router.push("/clients");
        }
      } catch (e) {
        alert(' ');
        console.log('[ERR]',e)
        //this.$localStorage.clear();
        //this.$error(   onLogin: ${e.toString()});
        //this.$showError( );
      }
    }
  },
  mounted(){
    this.$localStorage.clear(); 
  }
};
</script>

